import * as api from "../api/api";
import { useSetStatus, myStore } from "../store/store";
export function useAuth() {
  const setStatus = useSetStatus();
  const register = async (registerDataFromModal) => {
    const { registerEmail, registerPassword } = registerDataFromModal;

    const payload = {
      email: registerEmail,
      password: registerPassword,
    };
    const data = await api.fetchApi("/registerUser", "POST", payload);
    return data;
  };

  const auth = async (authDataFromModal) => {
    const { loginEmail, loginPassword } = authDataFromModal;

    const payload = {
      email: loginEmail,
      password: loginPassword,
    };
    const data = await api.fetchApi("/authUser", "POST", payload);
    if (data.ok) {
      localStorage.setItem("token", data.data.jwt_token);
      setStatus("auth");
    }
    return data;
  };

  const recover = async (email) => {
    const payload = { email: email };
    const data = await api.fetchApi("/fogotPassword", "POST", payload);
    return data;
  };

  const sendRecover = async (recoverData) => {
    const { token, password } = recoverData;
    const payload = { new_pass: password, token };
    const data = await api.fetchApi("/resetPassword", "POST", payload);
    return data;
  };

  const addCart = async (id) => {
    const payload = { id_product: id };
    const res = await api.fetchApi("/auth/basket/add", "POST", payload);
    if (res.ok) {
      myStore.setState((state) => ({
        cartDetails: [...state.cartDetails, id],
      }));
    }
    return res;
  };

  const remElemCart = async (id) => {
    const payload = { delete_item_id: id };
    const res = await api.fetchApi("/auth/basket/item", "DELETE", payload);
    if (res.ok) {
      myStore.setState((state) => ({
        cartDetails: state.cartDetails.filter((itemId) => itemId !== id),
      }));
    }
    return res;
  };

  const getPayLink = async () => {
    const res = await api.fetchApi("/auth/purchase", "GET");
    if (typeof res === "string") return res.trim();
    const url = res?.url ?? res?.data?.url ?? res?.data;
    if (typeof url === "string") return url.trim();
    throw new Error("Ошибка");
  };

  const extendCouse = async (payload) => {
    const res = await api.fetchApi("/auth/purchase/extension", "POST", {
      course_id: payload,
    });
    console.log(res);
    if (typeof res === "string") return res.trim();
    const url = res?.url ?? res?.data?.url ?? res?.data;
    if (typeof url === "string") return url.trim();
    throw new Error("Ошибка");
  };

  return {
    extendCouse,
    getPayLink,
    register,
    auth,
    recover,
    sendRecover,
    addCart,
    remElemCart,
  };
}
