import styles from "./ModalLogin.module.css";
import { useState, useEffect } from "react";
import { NavLink, useNavigate } from "react-router-dom";
import { useAuth } from "../hooks/useAuth";
import { useForm } from "react-hook-form";
import { Loader } from "../components/Loader";
import { myStore } from "../store/store";

export default function ModalLogin() {
  const check = myStore((s) => s.check);
  const [showPassword, setShowPassword] = useState(false);
  const { auth } = useAuth();
  const navigate = useNavigate();
  const { register, setError, handleSubmit, formState } = useForm({
    mode: "onSubmit",
    reValidateMode: "onSubmit",
  });
  const { errors } = formState;
  const [isLoading, setIsLoading] = useState(false);
  const emailRHFError = errors.email?.message;
  const passwordRHFError =
    errors.password?.message || errors.root?.server?.message;
  const hasEmailError = Boolean(emailRHFError);
  const hasPasswordError = Boolean(passwordRHFError);
  const [emailErrorText, setEmailErrorText] = useState("");
  const [passwordErrorText, setPasswordErrorText] = useState("");

  const onSubmit = async ({ email, password }) => {
    setIsLoading(true);
    const res = await auth({ loginEmail: email, loginPassword: password });

    if (!res.ok) {
      switch (res.message) {
        case "error comparison password":
          setError("password", {
            type: "server",
            message: "Неправильный пароль",
          });
          break;

        case "there is no user with this email, please register":
          setError("email", { type: "server", message: "Неверная почта" });
          break;

        default:
          setError("password", {
            type: "server",
            message: "Непредвиденная ошибка! \n Пожалуйста, повторите вход",
          });
          break;
      }
      setIsLoading(false);
      return;
    }
    check();
    setIsLoading(false);
    navigate("/");
  };

  useEffect(() => {
    if (emailRHFError) {
      setEmailErrorText(emailRHFError);
    }
  }, [emailRHFError]);

  useEffect(() => {
    if (passwordRHFError) {
      setPasswordErrorText(passwordRHFError);
    }
  }, [passwordRHFError]);

  return (
    <div className={styles.wrapper}>
      {isLoading && <Loader />}
      <div className={styles["wrapper_header"]}>
        <h2 onClick={() => navigate("/")}>JULIIAZHIVAEVA</h2>
        <button onClick={() => navigate("/")}>
          <svg
            width="28"
            height="28"
            viewBox="0 0 28 28"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              fillRule="evenodd"
              clipRule="evenodd"
              d="M0.43868 0.43868C0.71993 0.157779 1.10118 0 1.49868 0C1.89618 0 2.27743 0.157779 2.55868 0.43868L26.5587 24.4387C26.7061 24.576 26.8243 24.7416 26.9062 24.9256C26.9882 25.1096 27.0323 25.3082 27.0359 25.5096C27.0394 25.711 27.0024 25.9111 26.9269 26.0979C26.8515 26.2847 26.7392 26.4543 26.5968 26.5968C26.4543 26.7392 26.2847 26.8515 26.0979 26.9269C25.9111 27.0024 25.711 27.0394 25.5096 27.0359C25.3082 27.0323 25.1096 26.9882 24.9256 26.9062C24.7416 26.8243 24.576 26.7061 24.4387 26.5587L0.43868 2.55868C0.157779 2.27743 0 1.89618 0 1.49868C0 1.10118 0.157779 0.71993 0.43868 0.43868Z"
              fill="#FBE0F3"
            />
            <path
              fillRule="evenodd"
              clipRule="evenodd"
              d="M26.5587 0.43868C26.8396 0.71993 26.9974 1.10118 26.9974 1.49868C26.9974 1.89618 26.8396 2.27743 26.5587 2.55868L2.55872 26.5587C2.27437 26.8236 1.89827 26.9679 1.50967 26.961C1.12107 26.9542 0.750299 26.7968 0.475473 26.5219C0.200646 26.2471 0.0432229 25.8763 0.0363665 25.4877C0.0295101 25.0991 0.173756 24.723 0.438716 24.4387L24.4387 0.43868C24.72 0.157779 25.1012 0 25.4987 0C25.8962 0 26.2775 0.157779 26.5587 0.43868Z"
              fill="#FBE0F3"
            />
          </svg>
        </button>
      </div>
      <div className={styles.container}>
        <div className={styles.buttons}>
          <NavLink
            to="/login"
            className={({ isActive }) =>
              `${styles["nav-button"]} ${isActive ? styles.selected : ""}`
            }
          >
            Вход
          </NavLink>
          <NavLink
            to="/register"
            className={({ isActive }) =>
              `${styles["nav-button"]} ${isActive ? styles.selected : ""}`
            }
          >
            Регистрация
          </NavLink>
        </div>
        <h3 className={styles.title}>С возвращением!</h3>
        <form
          className={styles["login-form"]}
          onSubmit={handleSubmit(onSubmit)}
          noValidate
        >
          <div className={styles["input_container"]}>
            <input
              type="email"
              placeholder="Почта"
              {...register("email", {
                required: "Введите почту",
                pattern: {
                  value: /^[A-Za-z0-9._%+-]+@[A-Za-z0-9.-]+\.[A-Za-z]{2,}$/,
                  message: "Неверный формат почты, пример - ivanova@mail.ru",
                },
              })}
              className={`${styles.input} ${errors.email?.message ? styles["error_border"] : ""}`}
            />
            <p
              className={`${styles.error} ${hasEmailError ? styles.visible : ""}`}
            >
              {emailErrorText}
            </p>
            <div className={styles["input_wrapper"]}>
              <input
                type={showPassword ? "text" : "password"}
                placeholder="Пароль"
                {...register("password", {
                  required: "Введите пароль",
                })}
                className={`${styles.input} ${errors.password?.message && errors.password.type !== "server" ? styles["error_border"] : ""}`}
              />
              <button
                onClick={() => setShowPassword(!showPassword)}
                type="button"
                className={styles["toggle"]}
              >
                <img src="/icons/eye.svg" alt="" />
              </button>
            </div>
          </div>
          <div className={styles.utils}>
            <p
              className={`${styles.error} ${hasPasswordError ? styles.visible : ""}`}
            >
              {passwordErrorText}
            </p>
            <p
              className={styles["forgot_password"]}
              onClick={() => navigate("/RecoverForm")}
            >
              Забыли пароль?
            </p>
          </div>
          <button type="submit" className={styles.btn}>
            ВХОД
            <svg
              width="18"
              height="21"
              viewBox="0 0 18 21"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                d="M16.9672 0.900716C16.9122 0.351172 16.4222 -0.0497718 15.8726 0.00518263L6.91728 0.900716C6.36774 0.955671 5.96679 1.44571 6.02175 1.99526C6.0767 2.5448 6.56675 2.94575 7.11629 2.89079L15.0766 2.09476L15.8726 10.0551C15.9276 10.6046 16.4176 11.0055 16.9672 10.9506C17.5167 10.8956 17.9176 10.4056 17.8627 9.85605L16.9672 0.900716ZM0.774414 19.5752L1.54837 20.2084L16.7461 1.63346L15.9721 1.00022L15.1982 0.366982L0.00045675 18.942L0.774414 19.5752Z"
                fill="#FBE0F3"
              />
            </svg>
          </button>
        </form>
      </div>
    </div>
  );
}
